@import "~styles/variables";  // '~' informs sass-loader that is not relative path

#footer {
	height: 80px;
	background: $almost-black;
	font-family: 'Open-sans';
	font-size: $basic; }

.footer__menu {
	float: left;
	margin-top: 30px;
	margin-left: 50px;

	span {
		color: $brownish-grey;
		padding-left: 10px;
		text-transform: uppercase; }

	ul {
		display: inline-flex;
		list-style: none;
		flex-direction: row;
		margin: 0;
		padding: 0;

		li {
			padding-left: 20px;

			&:hover a {
				color: $white; } }

		a {
			color: $cool-grey;
			text-decoration: none;
			text-transform: uppercase; } } }

.company {
	color: $brownish-grey;
	float: right;
	margin-top: 30px;
	padding-right: 50px;
	text-transform: uppercase; }
