@import "~styles/variables";  // '~' informs sass-loader that is not relative path

#header {
	background: $almost-black;
	height: 50px;
	width: 100%; }

.header__logo {
	padding-top: 5px;
	height: 35px;
	margin: 0 auto;
	text-align: center;
	display: block;
	position: absolute;
	left: 50%;
	transform: translateX(-50%); }

.header__btn-back {
	color: $desert;
	float: left;
	cursor: pointer;
	text-transform: uppercase;
	margin: 14px 0 0 0;
	letter-spacing: 9px;
	&::before {
		content: ' ';
		float: left;
		display: block;
		border-bottom: 1px solid $desert;
		width: 30px;
		height: 9px;
		margin: 0 10px 0 0; }
	&:hover {
		transform: translateX(-5px);
		transition: all 0.4s; } }

.header__arrow {
	margin-left: 30px;
	margin-top: 18px;
	float: left; }
